<template>
  <v-form ref="form" v-model="valid" lazy-validation>
    <v-container>
      <v-card class="mx-auto my-12">
        <v-container>
          <v-row justify="space-around">
            <v-col :key="1" cols="12" sm="4">
              <v-combobox
                :items="formdata.bireyhane.items"
                v-model="formdata.bireyhane.selection"
                item-text="text"
                return-object
                @change="Mdisabledstatus"
                label="Örnekleme Birimini Seçin"
                outlined
                dense
              ></v-combobox>
            </v-col>
            <v-col :key="2" cols="12" sm="4">
              <v-combobox
                :items="formdata.qcins.items"
                v-model="formdata.qcins.selection"
                item-color="deep-purple"
                label="Cinsiyet"
                :disabled="formdata.bireyhane.disabled"
                multiple
                outlined
                clearable
                dense
              >
                <template #selection="{ item }">
                  <v-chip
                    color="deep-purple darken-2"
                    text-color="white"
                    small
                    close
                    :disabled="formdata.bireyhane.disabled"
                    @click:close="deleteChip(item, formdata.qcins.selection)"
                  >
                    {{ item.text }}
                  </v-chip>
                </template>
              </v-combobox>
            </v-col>
            <v-col :key="3" cols="12" sm="4">
              <v-combobox
                :items="formdata.kirkent.items"
                v-model="formdata.kirkent.selection"
                label="Kır-Kent"
                item-color="deep-purple"
                multiple
                outlined
                clearable
                dense
              >
                <template #selection="{ item }">
                  <v-chip
                    color="deep-purple darken-2"
                    text-color="white"
                    small
                    :close="true"
                    @click:close="deleteChip(item, formdata.kirkent.selection)"
                  >
                    {{ item.text }}
                  </v-chip>
                </template>
              </v-combobox>
            </v-col>
          </v-row>

          <v-row justify="space-around">
            <v-col :key="6" cols="12" sm="4">
              <v-combobox
                :items="formdata.ses.items"
                v-model="formdata.ses.selection"
                @change="Msesselection"
                label="Ses grupları"
                outlined
                dense
              ></v-combobox>
            </v-col>
            <v-col :key="5" cols="12" sm="4">
              <v-combobox
                :items="formdata.ses.defaultvalues"
                v-model="formdata.ses.defaultselection"
                item-color="deep-purple"
                label="Ses"
                placeholder="Ses grubu seçiniz."
                outlined
                multiple
                clearable
                dense
              >
                <template #selection="{ item }">
                  <v-chip
                    color="deep-purple darken-2"
                    text-color="white"
                    small
                    :close="true"
                    @click:close="
                      deleteChip(item, formdata.ses.defaultselection)
                    "
                  >
                    {{ item.text }}
                  </v-chip>
                </template>
              </v-combobox>
            </v-col>
            <v-col :key="9" cols="12" sm="4">
              <v-combobox
                :items="formdata.egitim.items"
                v-model="formdata.egitim.selection"
                :disabled="formdata.bireyhane.disabled"
                item-color="deep-purple"
                label="Eğitim Durumu"
                :auto-select-first="true"
                clearable
                outlined
                multiple
                counter
                dense
              >
                <template v-slot:selection="{ item, index }">
                  <v-chip
                    v-if="index === 0"
                    color="deep-purple darken-2"
                    text-color="white"
                    small
                    :disabled="formdata.bireyhane.disabled"
                  >
                    <span>{{ item.text }}</span>
                  </v-chip>
                  <span v-if="index === 1" class="grey--text caption">
                    (+{{ formdata.egitim.selection.length - 1 }} diğer)
                  </span>
                </template>
              </v-combobox>
            </v-col>
          </v-row>
        </v-container>
      </v-card>

      <v-row justify="space-around">
        <v-col cols="12" sm="6">
          <v-card>
            <v-container>
              <v-row justify="space-around">
                <v-container>
                  <v-combobox
                    :items="formdata.cografya.items"
                    v-model="formdata.cografya.selection"
                    @change="Mcografyaselection"
                    label="Coğrafi Kapsam"
                    outlined
                    dense
                  ></v-combobox>
                </v-container>
              </v-row>
              <v-row justify="space-around">
                <v-container>
                  <v-combobox
                    :items="formdata.cografya.defaultvalues"
                    v-model="formdata.cografya.defaultselection"
                    placeholder="Coğrafi Kapsam seçiniz."
                    item-color="deep-purple"
                    :auto-select-first="true"
                    counter
                    outlined
                    multiple
                    clearable
                    dense
                  >
                    <template v-slot:selection="{ item, index }">
                      <v-chip
                        v-if="index === 0"
                        color="deep-purple darken-2"
                        text-color="white"
                        :small="true"
                      >
                        <span>{{ item.text }}</span>
                      </v-chip>
                      <span v-if="index === 1" class="grey--text caption">
                        (+{{ formdata.cografya.defaultselection.length - 1 }}
                        diğer)
                      </span>
                    </template>
                  </v-combobox>
                </v-container>
              </v-row>
            </v-container>
          </v-card>
        </v-col>

        <v-col cols="12" sm="6">
          <v-card>
            <v-container>
              <v-row justify="space-around">
                <v-container>
                  <v-combobox
                    :items="formdata.qyas.items"
                    v-model="formdata.qyas.selection"
                    @change="Myasselection"
                    label="Yaş Grupları"
                    :disabled="formdata.bireyhane.disabled"
                    outlined
                    dense
                  ></v-combobox>
                </v-container>
              </v-row>
              <v-row justify="space-around">
                <v-container>
                  <v-combobox
                    :items="formdata.qyas.defaultvalues"
                    v-model="formdata.qyas.defaultselection"
                    :disabled="formdata.bireyhane.disabled"
                    item-color="deep-purple"
                    placeholder="Yaş grubu seçiniz."
                    :auto-select-first="true"
                    clearable
                    outlined
                    multiple
                    counter
                    dense
                  >
                    <template v-slot:selection="{ item, index }">
                      <v-chip
                        v-if="index === 0"
                        color="deep-purple darken-2"
                        text-color="white"
                        small
                        :disabled="formdata.bireyhane.disabled"
                      >
                        <span>{{ item.text }}</span>
                      </v-chip>
                      <span v-if="index === 1" class="grey--text caption">
                        (+{{ formdata.qyas.defaultselection.length - 1 }} diğer)
                      </span>
                    </template>
                  </v-combobox>
                </v-container>
              </v-row>
            </v-container>
          </v-card>
        </v-col>
      </v-row>
      <v-spacer></v-spacer>
      <v-row>
        <v-col class="text-right">
          <v-btn
            :disabled="!valid"
            color="indigo accent-1"
            class="pa-2"
            @click="MsendForm"
          >
            Hesapla
          </v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import { FormData } from "./FormData";

export default {
  mixins: [FormData],
  data() {
    return {
      valid: true,
    };
  },
  methods: {
    Msesselection(e) {
      this.formdata.ses.defaultselection = [];
      this.formdata.ses.defaultvalues = [];
      if (e.value == 1) {
        this.formdata.ses.defaultvalues = this.formdata.ses.sesgrup.items;
        this.formdata.ses.defaultselection = this.formdata.ses.sesgrup.selection;
      }
      if (e.value == 2) {
        this.formdata.ses.defaultvalues = this.formdata.ses.sesacik.items;
        this.formdata.ses.defaultselection = this.formdata.ses.sesacik.selection;
      }
    },
    Mdisabledstatus(e) {
      return e.value == "2"
        ? (this.formdata.bireyhane.disabled = true)
        : (this.formdata.bireyhane.disabled = false);
    },
    Myasselection(e) {
      this.formdata.qyas.defaultselection = [];
      this.formdata.qyas.defaultvalues = [];
      if (e.value == 1) {
        this.formdata.qyas.defaultvalues = this.formdata.qyas.besliyas.items;
        this.formdata.qyas.defaultselection = this.formdata.qyas.besliyas.selection;
      }
      if (e.value == 2) {
        this.formdata.qyas.defaultvalues = this.formdata.qyas.acikyas.items;
        this.formdata.qyas.defaultselection = this.formdata.qyas.acikyas.selection;
      }
      /* Buraya diğer yaş grupları için Güncemden seçenekler gelecek. */
    },
    Mcografyaselection(e) {
      this.formdata.cografya.defaultselection = [];
      this.formdata.cografya.defaultvalues = [];
      if (e.value == 1) {
        this.formdata.cografya.defaultvalues = this.formdata.cografya.nuts1.items;
        this.formdata.cografya.defaultselection = this.formdata.cografya.nuts1.selection;
      }
      if (e.value == 2) {
        this.formdata.cografya.defaultvalues = this.formdata.cografya.nuts2.items;
        this.formdata.cografya.defaultselection = this.formdata.cografya.nuts2.selection;
      }
      if (e.value == 3) {
        this.formdata.cografya.defaultvalues = this.formdata.cografya.il.items;
        this.formdata.cografya.defaultselection = this.formdata.cografya.il.selection;
      }
    },
    deleteChip(itemNeedToRemove, array) {
      for (let i = 0; i < array.length; i += 1) {
        if (array[parseInt(i, 10)] === itemNeedToRemove) {
          array.splice(i, 1);
        }
      }
    },
    MsendForm() {
      console.log(this.formdata);
    },
  },
};
</script>
